___CSS_LOADER_EXPORT___.push([module.id, `.links {
    --width: 15rem;
    --indicator-gap: .5rem;
    --indicator-width: 2rem;
    --link-height: 3.5rem;

    position: absolute;
    inset: 0;

    width: var(--width);
    height: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    translate: calc(-1 * var(--width));
    transition: translate .2s ease-out;
    
    z-index: 20;

    opacity: 1;
    pointer-events: all;

    &.hide {
        pointer-events: none;
        & .indicator {
            opacity: 0;
        }
    }

    & .indicator {
        position: absolute;
        left: calc(100% + var(--indicator-gap));
    
        width: .25rem;
        height: 16rem;

        background-color: var(--clr-btn);
        border-radius: .3rem;
        opacity: .5;
    
        transition: translate .1s, opacity .4s ease-in;

        display: flex;
        align-items: center;

        & i {
            position: relative;
            width: 2rem;
            height: 100%;
            display: flex;
            align-items: center;
            left: 1rem;
            font-size: 1.2rem;
            color: var(--clr-btn);
        }
    }
    
    & .filler {
        position: absolute;
        inset: 0;
        background-color: var(--clr-link-filler);
    }
    
    &:hover, 
    & .indicator:hover {
        translate: 0;

        & .indicator {
            translate: 
                calc(
                    (var(--indicator-width) + var(--indicator-gap)) 
                    * -1
                );
            opacity: 0;
        }

        & .filler {
            border-right: 1px solid var(--clr-link-filler-border);
            backdrop-filter: blur(.75rem);
            z-index: 9;
        }
    }
}

.sub-links {
    position: absolute;
    inset: 0;

    width: var(--width);
    height: 100vh;
    padding: 1rem 0;
    padding-top: var(--padding, 1rem);
    
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    
    translate: calc(var(--width) * -1);
    transition: translate .2s .1s ease-out;
    
    z-index: 8;
    
    .link-header:hover + &,
    &:hover {
        translate: var(--width);
    }

    & .filler {
        width: calc(var(--width) * 2);
        height: inherit;
        position: absolute;
        left: -100%;
        background-color: var(--clr-link-filler);
        border-right: 1px solid var(--clr-link-filler-border);
        backdrop-filter: blur(.25rem);
        z-index: 9;
    }

    & a {
        --link-margin: 0rem;

        text-decoration: none;
        width: calc(var(--width) - var(--link-margin));
        height: var(--link-height);
        margin: 0 var(--link-margin);
        padding-left: calc(2.5rem - var(--link-margin));

        color: var(--clr-secondary);
        font-size: 1.2rem;
        font-weight: bold;
        background: transparent;
        transition: .2s;
        z-index: 10;
    
        display: flex;
        align-items: center;
        
        &:hover {
            text-decoration: none;
            color: var(--clr-title-2);
            background: linear-gradient(
                to right,
                transparent,
                var(--clr-link-filler)
            );
        }
    }
}

.link-header {
    width: var(--width);
    height: var(--link-height);
    padding: 0 1rem;
    
    transition: .25s;
    z-index: 10;
    
    display: flex;
    align-items: center;
    
    &::before {
        content: '';
        position: absolute;
        background: 
            linear-gradient(
                to right, 
                var(--clr-link-grad-1), 
                var(--clr-link-grad-2)
            );
        inset: 0;
        opacity: 0;
        transition: .25s;
        width: var(--width);
        height: var(--link-height);
        border-radius: 0 .5rem .5rem 0;
    }

    &:hover,
    &:has(+.sub-links:hover) {
        translate: 1rem;

        &::before {
            width: calc(var(--width) + 1rem);
            translate: -1rem;
            opacity: .3;
        } 
    }    
    
    & i {
        position: relative;
        left: 1rem;
        width: 1rem;    
        color: var(--clr-secondary);
    }

    & a {
        position: relative;
        left: 2.5rem;
        font-size: 1.2rem;
        font-weight: bold;
        color: var(--clr-secondary);
    } 
}`, "",{"version":3,"sources":["webpack://./components/Links.css"],"names":[],"mappings":"AAAA;IACI,cAAc;IACd,sBAAsB;IACtB,uBAAuB;IACvB,qBAAqB;;IAErB,kBAAkB;IAClB,QAAQ;;IAER,mBAAmB;IACnB,YAAY;;IAEZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;;IAEvB,kCAAkC;IAClC,kCAAkC;;IAElC,WAAW;;IAEX,UAAU;IACV,mBAAmB;;IAEnB;QACI,oBAAoB;QACpB;YACI,UAAU;QACd;IACJ;;IAEA;QACI,kBAAkB;QAClB,uCAAuC;;QAEvC,aAAa;QACb,aAAa;;QAEb,gCAAgC;QAChC,oBAAoB;QACpB,WAAW;;QAEX,8CAA8C;;QAE9C,aAAa;QACb,mBAAmB;;QAEnB;YACI,kBAAkB;YAClB,WAAW;YACX,YAAY;YACZ,aAAa;YACb,mBAAmB;YACnB,UAAU;YACV,iBAAiB;YACjB,qBAAqB;QACzB;IACJ;;IAEA;QACI,kBAAkB;QAClB,QAAQ;QACR,wCAAwC;IAC5C;;IAEA;;QAEI,YAAY;;QAEZ;YACI;;;;iBAIK;YACL,UAAU;QACd;;QAEA;YACI,qDAAqD;YACrD,6BAA6B;YAC7B,UAAU;QACd;IACJ;AACJ;;AAEA;IACI,kBAAkB;IAClB,QAAQ;;IAER,mBAAmB;IACnB,aAAa;IACb,eAAe;IACf,iCAAiC;;IAEjC,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,mBAAmB;;IAEnB,kCAAkC;IAClC,sCAAsC;;IAEtC,UAAU;;IAEV;;QAEI,uBAAuB;IAC3B;;IAEA;QACI,6BAA6B;QAC7B,eAAe;QACf,kBAAkB;QAClB,WAAW;QACX,wCAAwC;QACxC,qDAAqD;QACrD,6BAA6B;QAC7B,UAAU;IACd;;IAEA;QACI,mBAAmB;;QAEnB,qBAAqB;QACrB,8CAA8C;QAC9C,0BAA0B;QAC1B,4BAA4B;QAC5B,+CAA+C;;QAE/C,2BAA2B;QAC3B,iBAAiB;QACjB,iBAAiB;QACjB,uBAAuB;QACvB,eAAe;QACf,WAAW;;QAEX,aAAa;QACb,mBAAmB;;QAEnB;YACI,qBAAqB;YACrB,yBAAyB;YACzB;;;;aAIC;QACL;IACJ;AACJ;;AAEA;IACI,mBAAmB;IACnB,0BAA0B;IAC1B,eAAe;;IAEf,gBAAgB;IAChB,WAAW;;IAEX,aAAa;IACb,mBAAmB;;IAEnB;QACI,WAAW;QACX,kBAAkB;QAClB;;;;;aAKK;QACL,QAAQ;QACR,UAAU;QACV,gBAAgB;QAChB,mBAAmB;QACnB,0BAA0B;QAC1B,8BAA8B;IAClC;;IAEA;;QAEI,eAAe;;QAEf;YACI,gCAAgC;YAChC,gBAAgB;YAChB,WAAW;QACf;IACJ;;IAEA;QACI,kBAAkB;QAClB,UAAU;QACV,WAAW;QACX,2BAA2B;IAC/B;;IAEA;QACI,kBAAkB;QAClB,YAAY;QACZ,iBAAiB;QACjB,iBAAiB;QACjB,2BAA2B;IAC/B;AACJ","sourcesContent":[".links {\n    --width: 15rem;\n    --indicator-gap: .5rem;\n    --indicator-width: 2rem;\n    --link-height: 3.5rem;\n\n    position: absolute;\n    inset: 0;\n\n    width: var(--width);\n    height: 100%;\n    \n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    \n    